import React, { useState, useRef } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import jsPDF from 'jspdf';
import axios from 'axios';
import './App.css'
import { apiUrl } from './config';

const PDFGenerator = () => {
  const [submitted, setSubmitted] = useState(false);
  const [currentSlide, setCurrentSlide] = useState(0);
  const sliderRef = useRef(null);
  const [StartUpName, setStartUpName] = useState('');
  const [email, setEmail] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  const [Problem, setProblem] = useState('');
  const [Resolve, setResolve] = useState('');
  const [Costomer, setCostomerBenefits] = useState('');
  const [Revenue, setRevenue] = useState('');
  const [Product, setProduct] = useState('');
  const [Resources, setResources] = useState('');
  const [Unique, setUnique] = useState('');
  const [Competitors, setCompetitors] = useState('');
  const [Target, setTarget] = useState('');


 
  const sliderSettings = {
    dots: true,
    arrows: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    afterChange: (current) => setCurrentSlide(current)
  };

  const handlePreviousSlide = () => {
    if (sliderRef.current) {
      sliderRef.current.slickPrev();
    }
  };

  const handleNextSlide = (e) => {

    e.preventDefault()

    if(!StartUpName || !email)
    {
      alert('Startup Name and Email is Required')
    }else{
      if (sliderRef.current) {
        sliderRef.current.slickNext();
      }
    }


  };

  const handleGeneratePDF = () => {
    const doc = new jsPDF();
    
 // Define initial y-coordinate
let y = 10;

// Add content to the PDF
doc.text(`StartUpName: ${StartUpName}`, 10, y);
y += 10; // Increase y-coordinate for the next line
doc.text(`Email: ${email}`, 10, y);
y += 10;
doc.text(`Category: ${selectedCategory}`, 10, y);
y += 10;
doc.text(`Problem: ${Problem}`, 10, y);
y += 10;
doc.text(`Resolve: ${Resolve}`, 10, y);
y += 10;
doc.text(`Customer: ${Costomer}`, 10, y); // Corrected spelling from Costomer to Customer
y += 10;
doc.text(`Revenue: ${Revenue}`, 10, y);
y += 10;
doc.text(`Product: ${Product}`, 10, y);
y += 10;
doc.text(`Resources: ${Resources}`, 10, y);
y += 10;
doc.text(`Unique: ${Unique}`, 10, y);
y += 10;
doc.text(`Competitors: ${Competitors}`, 10, y);
y += 10;
doc.text(`Target: ${Target}`, 10, y);



    // Save the PDF
    doc.save('user_data.pdf');
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post(`${apiUrl}/business`,{
        StartUpName,
        email,
        selectedCategory,
        Problem,
        Resolve,
        Costomer,
        Revenue,
        Product,
        Resources,
        Unique,
        Competitors,
        Target
      })

      if(res)
      {
        alert('thanks for submit Your Business Model');
        setSubmitted(true);
      }
    } catch (error) {
      alert('Internal Server Error');
    }
  };

  const totalSlides = 6; // Replace 3 with the actual total number of slides

  return (
    <>
      <div className='container transparent-input' style={{ marginTop:'5rem',height:'70vh' }}>
        <h1 className='text-center text-white' style={{paddingTop:'3rem'}}> Business Model Generator Tools</h1>
        <hr />
        <div className='row justify-content-center'>
          <div className='col-md-12'>

            <form>
              <Slider ref={sliderRef} {...sliderSettings}>
                {/* Your slider content here */}
                <div>
                <div className="row">
              <div className="col-md-6">
                <div className="form-group">
                  <label htmlFor="startupName" className='text-white'>StartUp Name</label>
                  <input type="text" className="form-control transparent-input" id="startupName" value={StartUpName} onChange={(e) => setStartUpName(e.target.value)} placeholder="Enter startup name" required />
                </div>
              </div>
              <div className="col-md-6">
                <div className="form-group">
                  <label htmlFor="email" className='text-white'>Email address</label>
                  <input type="email" className="form-control transparent-input" id="email" value={email} onChange={(e) => setEmail(e.target.value)} placeholder="name@example.com" required />
                </div>
              </div>
            </div>
                </div>
                <div>
                <div className='row'>
            <div className='col-md-6'>
            <div className="form-group">
            <label htmlFor="exampleFormControlSelect1" className='text-white'>StartUp Category</label>
            <select  value={selectedCategory} onChange={(e)=>setSelectedCategory(e.target.value)} className="form-control transparent-input" id="exampleFormControlSelect1" required>
            <option value={''} required>Select Category</option>
              <option value={'E-Commerce'} >E-Commerce</option>
              <option value={'Construction'}>Construction</option>
              <option value={'Media'}>Media</option>
            </select>
          </div>
            </div>

            <div className='col-md-6'>
            <div className="form-group">
              <label htmlFor="exampleFormControlTextarea1" className='text-white'>What specific problem or pain point are you addressing ?
              </label>
              <textarea className="form-control transparent-input" value={Problem}  onChange={(e)=> setProblem(e.target.value)} id="exampleFormControlTextarea1" rows="3" required></textarea>
             </div>
            </div>
          </div>
           </div>
           <div>
           <div className='row'>
      <div className='col-md-6'>
      <div className="form-group">
            <label htmlFor="exampleFormControlTextarea1" className='text-white'>How you are resolving these problems and pain points?
        </label>
            <textarea className="form-control transparent-input" value={Resolve}  onChange={(e)=> setResolve(e.target.value)} id="exampleFormControlTextarea1" rows="3" required></textarea>
       </div>
      </div>

      <div className='col-md-6'>
      <div className="form-group">
            <label htmlFor="exampleFormControlTextarea1" className='text-white'>What benefits or value do you offer to customers that they can't get elsewhere?
        </label>
            <textarea className="form-control transparent-input" value={Costomer}  onChange={(e)=> setCostomerBenefits(e.target.value)} id="exampleFormControlTextarea1" rows="3" required></textarea>
       </div>
      </div>
     </div>
           </div>
           <div>
            
     <div className='row'>
      <div className='col-md-6'>
      <div className="form-group">
          <label htmlFor="exampleFormControlTextarea1" className='text-white'>How do you plan to generate revenue?
            </label>
          <textarea className="form-control transparent-input" value={Revenue}  onChange={(e)=> setRevenue(e.target.value)} id="exampleFormControlTextarea1" rows="3" required></textarea>
       </div>
      </div>

      <div className='col-md-6'>
      <div className="form-group">
        <label htmlFor="exampleFormControlTextarea1" className='text-white'>How do you plan to distribute or deliver your product/service to customers?
        </label>
        <textarea className="form-control transparent-input" value={Product}  onChange={(e)=> setProduct(e.target.value)} id="exampleFormControlTextarea1" rows="3" required></textarea>
      </div>
      </div>
     </div>
           </div>
           <div>
           <div className='row'>
      <div className='col-md-6'>
      <div className="form-group">
    <label htmlFor="exampleFormControlTextarea1" className='text-white'>What resources (financial, human, technological, etc.) do you need to operate your business?
        </label>
    <textarea className="form-control transparent-input" value={Resources}  onChange={(e)=> setResources(e.target.value)} id="exampleFormControlTextarea1" rows="3" required></textarea>
     </div>
      </div>

      <div className='col-md-6'>
      <div className="form-group">
          <label htmlFor="exampleFormControlTextarea1" className='text-white'>What unique advantage or resource sets your startup apart from competitors, and which key metrics will you track to measure the strength and sustainability of this advantage?
              </label>
          <textarea className="form-control transparent-input" value={Unique}  onChange={(e)=> setUnique(e.target.value)} id="exampleFormControlTextarea1" rows="3" required></textarea>
       </div>
      </div>
     </div>
           </div>
           <div>
           <div className='row'>
      <div className='col-md-6'>
      <div className="form-group">
    <label htmlFor="exampleFormControlTextarea1" className='text-white'> Who are your Existing Competitors?
         </label>
    <textarea className="form-control transparent-input" value={Competitors}  onChange={(e)=> setCompetitors(e.target.value)} id="exampleFormControlTextarea1" rows="3" required></textarea>
  </div>
      </div>

      <div className='col-md-6'>
      <div className="form-group">
    <label htmlFor="exampleFormControlTextarea1" className='text-white'> Who are your target customers, describe your ideal customer personal?
   </label>
    <textarea className="form-control transparent-input" value={Target}  onChange={(e)=> setTarget(e.target.value)} id="exampleFormControlTextarea1" rows="3" required></textarea>
  </div>
      </div>
    </div>
           </div>
                
        </Slider>
            </form>
            <div className='text-center'>
              <div className='d-inline-block float-left cursor-pointer'>
                {currentSlide !== 0 && (
                  <button className='btn btn-primary ' onClick={handlePreviousSlide}>Previous</button>
                )}
              </div>
              <div className='d-inline-block float-right cursor-pointer'>
                {currentSlide !== totalSlides - 1 && (
                  <button className='btn btn-primary ' onClick={handleNextSlide}>Next</button>
                )}
                </div>
                <div className='d-inline-block text-center cursor-pointer'>
                {currentSlide === totalSlides - 1 && !submitted && (
                  <button className='btn btn-success mt-5 ' onClick={handleSubmit}>Submit</button>
                )}
                {submitted && currentSlide === totalSlides - 1 && (
                  <button className='btn btn-success mx-2 mt-5' onClick={handleGeneratePDF}>Generate PDF</button>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default PDFGenerator;
